import { html } from 'lit-element';
import { createRange } from '../../services/extensions/array.extensions';
/**
 * @param { StarRating } this
 */
export function renderStarRating() {
    return html `
        ${this.vertical
        ? html `<style>
                  #star-container {
                      flex-direction: column !important;
                  }
              </style>`
        : ''}
        <div id="star-container">
            ${createRange(0, this.max - 1).map((x) => html `<div
                        class="star"
                        ?hovered="${this.singleSelect && this.hoveredRating > x}"
                        ?selected="${this.values.includes(x + 1)}"
                        @click="${() => this.toggleRating(x + 1)}"
                        @pointerover="${() => (this.hoveredRating = x + 1)}"
                        @pointerout="${() => (this.hoveredRating = -1)}"
                    >
                        ${this.swords ? '' : ''}
                    </div>`)}
        </div>
    `;
}
