import { html } from 'lit-element';
import { CheckboxState } from '../../data/enumerations/checkbox-state';
import { MoodColors } from '../../data/enumerations/mood';
import { MusicSortingProperties } from '../../data/music-sorting-properties';
import { DropDownOption } from '../../native-components/drop-down/drop-down-option';
import { Instrumentation, InstrumentType, Language, Mood, MusicGenre, Participants, } from '../../obscuritas-media-manager-backend-client';
import { Icons } from '../../resources/inline-icons/icon-registry';
export function renderMusicFilter() {
    return html `
        <div id="search-heading">
            <div class="heading-label">Suche</div>
            <div class="icon-button" icon="${Icons.Revert}" tooltip="Zurcksetzen" @click="${() => this.resetAllFilters()}"></div>
        </div>
        <div id="search-panel">
            <div id="text-filter" class="filter" simple>
                <input
                    type="text"
                    id="search-input"
                    placeholder="Suchbegriff eingeben..."
                    oninput="this.dispatchEvent(new Event('change'))"
                    @change="${(e) => this.setfilterEntry('search', e.target.value)}"
                    .value="${this.filter.search || ''}"
                />
            </div>
            <div id="complete-filter" class="filter" simple>
                <label for="scales">Vollstndig: </label>
                <custom-toggle
                    id="show-complete-toggle"
                    .state="${this.filter.showComplete}"
                    threeValues
                    @toggle="${(e) => this.setfilterEntry('showComplete', e.detail)}"
                ></custom-toggle>
            </div>
            <div id="show-playlists-filter" class="filter" simple>
                <label for="scales">Playlists anzeigen: </label>
                <custom-toggle
                    id="show-playlists-toggle"
                    .state="${this.filter.showPlaylists}"
                    threeValues
                    @toggle="${(e) => this.setfilterEntry('showPlaylists', e.detail)}"
                ></custom-toggle>
            </div>
            <div id="show-deleted-filter" class="filter" simple>
                <label for="scales">Gelschte anzeigen: </label>
                <custom-toggle
                    id="show-deleted-toggle"
                    .state="${this.filter.showDeleted}"
                    threeValues
                    @toggle="${(e) => this.setfilterEntry('showDeleted', e.detail)}"
                ></custom-toggle>
            </div>
            <div id="mood-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Sortieren:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.Revert}"
                        tooltip="Zurcksetzen"
                        @click="${() => this.changeSorting('unset')}"
                    ></div>
                </div>
                <div id="sorting-container">
                    <drop-down
                        .options="${Object.entries(MusicSortingProperties).map((x) => DropDownOption.create({
        value: x[0],
        text: x[1],
        state: x[0] == this.sortingProperty ? CheckboxState.Ignore : CheckboxState.Forbid,
    }))}"
                        unsetText="Keine Sortierung"
                        maxDisplayDepth="5"
                        @selectionChange="${(e) => this.changeSorting(e.detail.option.value)}"
                    >
                    </drop-down>
                    <div
                        id="ascending-icon"
                        ?active="${this.sortingDirection == 'ascending'}"
                        class="icon-button"
                        icon="${Icons.Ascending}"
                        tooltip="Aufsteigend sortieren"
                        @click="${() => this.changeSorting(null, 'ascending')}"
                    ></div>
                    <div
                        id="descending-icon"
                        ?active="${this.sortingDirection == 'descending'}"
                        class="icon-button"
                        icon="${Icons.Descending}"
                        tooltip="Absteigend sortieren"
                        @click="${() => this.changeSorting(null, 'descending')}"
                    ></div>
                </div>
            </div>
            <div id="language-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Sprache:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('languages', CheckboxState.Require)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('languages', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <side-scroller>
                    ${Object.values(Language).map((type) => html ` <tri-value-checkbox
                                @valueChanged="${(e) => this.setFilterEntryValue('languages', type, e.detail.value)}"
                                class="icon-container"
                                .value="${this.filter.languages.states[type]}"
                            >
                                <div class="inline-icon " language="${type}"></div>
                            </tri-value-checkbox>`)}
                </side-scroller>
            </div>
            <div id="instrument-type-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Instrument Typen:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('instrumentTypes', CheckboxState.Require)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('instrumentTypes', CheckboxState.Forbid)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.Revert}"
                        tooltip="Zurcksetzen"
                        @click="${() => this.setArrayFilter('instrumentTypes', CheckboxState.Ignore)}"
                    ></div>
                </div>
                <side-scroller>
                    ${Object.values(InstrumentType).map((type) => html ` <tri-value-checkbox
                                class="icon-container"
                                allowThreeValues
                                @valueChanged="${(e) => this.setFilterEntryValue('instrumentTypes', type, e.detail.value)}"
                                .value="${this.filter.instrumentTypes.states[type]}"
                                .disabled="${!this.canFilterInstrumentType(type)}"
                            >
                                <div class="inline-icon" instrument-type="${type}"></div>
                            </tri-value-checkbox>`)}
                </side-scroller>
            </div>
            <div id="instrument-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Instrumente:</div>
                    <div class="icon-button" icon="${Icons.Popup}" @click="${() => this.showInstrumentFilterPopup()}"></div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('instruments', CheckboxState.Require)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('instruments', CheckboxState.Forbid)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.Revert}"
                        tooltip="Zurcksetzen"
                        @click="${() => this.setArrayFilter('instruments', CheckboxState.Ignore)}"
                    ></div>
                </div>
            </div>
            <div id="rating-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Bewertung:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('ratings', CheckboxState.Require)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('ratings', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <div class="filter-row">
                    <div class="filter-label">Show undefined:</div>
                    <custom-toggle
                        .state="${this.filter.ratings.states[0]}"
                        @toggle="${(e) => this.setFilterEntryValue('ratings', '0', e.detail)}"
                    ></custom-toggle>
                </div>
                <star-rating
                    max="5"
                    .values="${this.filter.ratings.ignored.map((x) => Number.parseInt(x))}"
                    @ratingChanged="${(e) => this.setFilterEntryValue('ratings', `${e.detail.rating}`, e.detail.include ? CheckboxState.Ignore : CheckboxState.Forbid)}"
                ></star-rating>
            </div>
            <div id="mood-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Stimmung:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('moods', CheckboxState.Ignore)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('moods', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <drop-down
                    @selectionChange="${(e) => this.setFilterEntryValue('moods', e.detail.option.value, e.detail.option.state)}"
                    .options="${Object.entries(Mood).map((x) => DropDownOption.create({
        value: x[0],
        text: x[1],
        color: MoodColors[x[0]],
        state: this.filter.moods.states[x[0]],
    }))}"
                    unsetText="Keine Eintrge ausgewhlt"
                    multiselect
                    threeValues
                    useToggle
                    maxDisplayDepth="5"
                >
                </drop-down>
            </div>
            <div id="genre-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">genres:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('genres', CheckboxState.Ignore)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('genres', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <drop-down
                    @selectionChange="${(e) => this.setFilterEntryValue('genres', e.detail.option.value, e.detail.option.state)}"
                    .options="${Object.entries(MusicGenre).map((x) => DropDownOption.create({ value: x[0], state: this.filter.genres.states[x[0]], text: x[1] }))}"
                    multiselect
                    useToggle
                    threeValues
                    unsetText="Keine Eintrge ausgewhlt"
                    maxDisplayDepth="5"
                >
                </drop-down>
            </div>
            <div id="instrumentation-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Instrumentverteilung:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('instrumentations', CheckboxState.Ignore)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('instrumentations', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <drop-down
                    @selectionChange="${(e) => this.setFilterEntryValue('instrumentations', e.detail.option.value, e.detail.option.state)}"
                    .options="${Object.values(Instrumentation).map((key) => DropDownOption.create({ value: key, text: key, state: this.filter.instrumentations.states[key] }))}"
                    useToggle
                    multiselect
                    maxDisplayDepth="5"
                >
                </drop-down>
            </div>
            <div id="participant-count-filter" class="filter">
                <div class="filter-heading">
                    <div class="heading-label">Mitgliederzahl:</div>
                    <div
                        class="icon-button"
                        icon="${Icons.SelectAll}"
                        tooltip="Alle auswhlen"
                        @click="${() => this.setArrayFilter('participants', CheckboxState.Require)}"
                    ></div>
                    <div
                        class="icon-button"
                        icon="${Icons.UnselectAll}"
                        tooltip="Alle abwhlen"
                        @click="${() => this.setArrayFilter('participants', CheckboxState.Forbid)}"
                    ></div>
                </div>
                <side-scroller>
                    ${Object.values(Participants).map((participants) => html ` <tri-value-checkbox
                                class="icon-container"
                                @valueChanged="${(e) => this.setFilterEntryValue('participants', participants, e.detail.value)}"
                                .value="${this.filter.participants.states[participants]}"
                            >
                                <div class="inline-icon" participants="${participants}"></div>
                            </tri-value-checkbox>`)}
                </side-scroller>
            </div>
        </div>
    `;
}
