import { CheckboxState } from './enumerations/checkbox-state';
export class FilterEntry {
    get required() {
        return Object.entries(this.states)
            .filter((x) => x[1] == CheckboxState.Require)
            .map((x) => x[0]);
    }
    get ignored() {
        return Object.entries(this.states)
            .filter((x) => x[1] == CheckboxState.Ignore)
            .map((x) => x[0]);
    }
    get forbidden() {
        return Object.entries(this.states)
            .filter((x) => x[1] == CheckboxState.Forbid)
            .map((x) => x[0]);
    }
    states;
    constructor(type, defaultValue = CheckboxState.Ignore) {
        this.states = {};
        for (var key of type)
            this.states[key] = defaultValue;
    }
    setKey(key, value) {
        this.states[key] = value;
    }
}
